import extractDomain from "extract-domain";

const urlConverter = (url) => {
  const urlsRegexList = [
    {
      // regex for Full URL Path
      //   Input: https://www.example.io, https://www.example.io/path/
      //   Output: https://www.example.io
      verify(input = "") {
        if (
          input &&
          input.indexOf("https://") > -1 &&
          input.match(/\./g) &&
          input.match(/\./g).length > 1
        )
          return true;
        return false;
      },
      output(input) {
        return `${input}`;
      },
      favicon(input) {
        return `https://www.${extractDomain(input)}/favicon.ico`;
      },
    },
    {
      // regex for No subdomain
      //   Input: https://example.io/path, https://example.com/path
      //   Output: https://www.example.io, https://www.example.com
      verify(input = "") {
        if (
          input &&
          input.indexOf("https://") > -1 &&
          input.match(/\./g) &&
          input.match(/\./g).length === 1
        )
          return true;
        return false;
      },
      output(input) {
        return `https://www.${input.replace("https://", "")}`;
      },
      favicon(input) {
        return `https://www.${extractDomain(input)}/favicon.ico`;
      },
    },
    {
      // regex for No Protocol
      //   Input: www.example.com, app.example.com/path
      //   Output: https://www.example.io, https://app.example.io
      verify(input = "") {
        if (
          input &&
          input.indexOf("https://") === -1 &&
          input.match(/\./g) &&
          input.match(/\./g).length > 1
        )
          return true;
        return false;
      },
      output(input) {
        return `https://${input}`;
      },
      favicon(input) {
        return `https://www.${extractDomain(input)}/favicon.ico`;
      },
    },
    {
      // regex for No Protocol, No subdomain
      //   Input: example.com/path
      //   Output: https://www.example.io
      verify(input = "") {
        if (
          input &&
          input.indexOf("https://") === -1 &&
          input.match(/\./g) &&
          input.match(/\./g).length === 1
        )
          return true;
        return false;
      },
      output(input) {
        return `https://www.${input}`;
      },
      favicon(input) {
        return `https://www.${input}/favicon.ico`;
      },
    },
  ];

  const urlObject = urlsRegexList.filter((obj, index) => {
    return obj.verify(url);
  });
  if (urlObject.length)
    return {
      url: urlObject[0].output(url),
      favicon: urlObject[0].favicon(url),
    };
  else return null;
};

export default urlConverter;
